// Send a msg to the websocket
var updateTime;
const sendMsg = (msg) => {
  clearInterval(updateTime)
    try {var prompt = JSON.parse(msg)
    if (prompt.type === "timedPrompt") {
      if (!prompt.currentTime) {
        var counter = parseInt(window.prompt("Starting time on prompt:"))
      } else {
        var counter = 1
      }
      
      updateTime = setInterval(() => {
        if (counter === prompt.timerLength) {
          clearInterval(updateTime)
          if (!prompt.nextPrompt) {
            sendMsg(null)
            return
          }
          var newPrompt = prompts[prompt.nextPrompt]
          newPrompt.currentTime = 1
          sendMsg(JSON.stringify(newPrompt))
          return
        }
        prompt.currentTime = counter
        counter ++
        msg = JSON.stringify(prompt)
        socket.send(msg);
      }, 1000)
      updateTime()
      
    }}
    catch {
    }
    socket.send(msg);
}
